import React, { Component } from 'react';

import { Calendar, momentLocalizer } from 'react-big-calendar';
import moment from 'moment';
import 'react-big-calendar/lib/css/react-big-calendar.css';
import axios from 'axios'
import "react-big-calendar/lib/css/react-big-calendar.css";

moment.locale('en-GB');
const localizer = momentLocalizer(moment);

class MainCalendar extends Component {



    constructor(props) {
        super(props)

        this.state = {
            cal_eventsAdmin: [
                //State is updated via componentDidMount
            ],
            cal_eventsEmp1: [
                //State is updated via componentDidMount
            ],
            cal_eventsEmp2: [
                //State is updated via componentDidMount
            ],
        }

    }

    convertDate = (date) => {
        return moment.utc(date).toDate()
    }

    componentDidMount() {


        axios.get('/schedule/calendar_admin')
            .then(response => {
                console.log(response.data);
                let appointments = response.data;

                for (let i = 0; i < appointments.length; i++) {
                    appointments[i].start = this.convertDate(appointments[i].start)
                    appointments[i].end = this.convertDate(appointments[i].end)
                }
                this.setState({
                    cal_eventsAdmin: appointments
                })
            })
            .catch(function (error) {
                console.log(error);
            });

        axios.get('/schedule/calendar_emp1')
            .then(response => {
                console.log(response.data);
                let appointments = response.data;

                for (let i = 0; i < appointments.length; i++) {
                    appointments[i].start = this.convertDate(appointments[i].start)
                    appointments[i].end = this.convertDate(appointments[i].end)
                }
                this.setState({
                    cal_eventsEmp1: appointments
                })
            })
            .catch(function (error) {
                console.log(error);
            });

        axios.get('/schedule/calendar_emp2')
            .then(response => {
                console.log(response.data);
                let appointments = response.data;

                for (let i = 0; i < appointments.length; i++) {
                    appointments[i].start = this.convertDate(appointments[i].start)
                    appointments[i].end = this.convertDate(appointments[i].end)
                }
                this.setState({
                    cal_eventsEmp2: appointments
                })
            })
            .catch(function (error) {
                console.log(error);
            });
    }

    handleEventClick = () => {
        const { event } = this.props;
        this.props.onSelect(event);
        console.log('event clicked!')
    };


    render() {

        const { cal_eventsAdmin } = this.state
        const { cal_eventsEmp1 } = this.state
        const { cal_eventsEmp2 } = this.state

        return (
            <div className="container" >
                <div className="row">
                    <div className="col-md-12">
                        <div className="container">
                            <div className="row">
                                {/* Admin's Calendar */}
                                <div className="col-md-4 " style={{ height: 700, marginLeft: "0px",  background: "rgba(3, 26, 77, 0.952)", border: "solid 1px white" }}>
                                    <h1 style={{ textAlign: "center", paddingTop: "15px", color: "white" }}>Mi Pao</h1>
                                    <hr style={{ background: "white" }}></hr>
                                    <Calendar style={{ height: "600px", marginBottom: "200px", background: "#f092d8", paddingTop: "15px" }}
                                        events={cal_eventsAdmin}
                                        step={15}
                                        selectable
                                        timeslots={4}
                                        defaultView='agenda'
                                        views={['month', 'week', 'day', 'agenda']}
                                        defaultDate={new Date()}
                                        localizer={localizer}
                                        min={new Date(2019, 10, 0, 9, 0, 0)}
                                        max={new Date(2019, 10, 0, 18, 0, 0)}
                                        onSelectEvent={event => alert(event.title)}
                                    />
                                </div>
                                {/* Admin's Calendar Ends*/}


                                {/* Employee #1 calendar */}
                                <div className="col-md-4" style={{ height: 700, marginLeft: "0px",  background: "rgba(3, 26, 77, 0.952)", border: "solid 1px white" }}>
                                    <h1 style={{ textAlign: "center", paddingTop: "15px", color: "white" }}>Guayita</h1>
                                    <hr style={{ background: "white" }}></hr>
                                    <Calendar style={{ height: "600px", marginBottom: "200px", background: "#a2d3fa", paddingTop: "15px" }}
                                        events={cal_eventsEmp1}
                                        step={30}
                                        defaultView='agenda'
                                        views={['month', 'week', 'day', 'agenda']}
                                        defaultDate={new Date()}
                                        localizer={localizer}
                                        min={new Date(2019, 10, 0, 7, 0, 0)}
                                        max={new Date(2019, 10, 0, 18, 0, 0)}
                                        onSelectEvent={event => alert(event.title)}

                                    />
                                </div>
                                {/* Employee #1 calendar Ends*/}


                                {/* Employee #2 calendar */}
                                <div className="col-md-4" style={{ height: 700, marginLeft: "0px",  background: "rgba(3, 26, 77, 0.952)", border: "solid 1px white" }}>
                                    <h1 style={{ textAlign: "center", paddingTop: "15px", color: "white" }}>Dianis</h1>
                                    <hr style={{ background: "white" }}></hr>
                                    <Calendar style={{ height: "600px", marginBottom: "200px", background: "#b170fa", paddingTop: "15px" }}
                                        events={cal_eventsEmp2}
                                        step={30}
                                        defaultView='agenda'
                                        views={['month', 'week', 'day', 'agenda']}
                                        defaultDate={new Date()}
                                        localizer={localizer}
                                        min={new Date(2019, 10, 0, 7, 0, 0)}
                                        max={new Date(2019, 10, 0, 18, 0, 0)}
                                        onSelectEvent={event => alert(event.title)}

                                    />
                                    {/* Employee #2 calendar Ends */}
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

export default MainCalendar;