import React, { Component } from 'react';
import API from '../utils/API';
import DateTimePicker from 'react-datetime-picker';


class AddAppointmentFormEmp2 extends Component {
    state = {
        title: '',
        allDay: false,
        start: '',
        end: '',
    }
    handleChange = (e) => {
        this.setState({
            [e.target.id]: e.target.value
        })
    }

    onChangeDatePicker = start => this.setState({ start })
    onChangeDatePickerEnd = end => this.setState({ end })

    handleSubmit = (e) => {
        e.preventDefault();
        // if (!this.state.title || !this.state.allDay || !this.state.start || !this.state.end
        // ) {
        //     return;
        // }

        API.addAppointmentEmp2({
            title: this.state.title,
            allDay: this.state.allDay,
            start: this.state.start,
            end: this.state.end,
        })
            // .then(alert("Your appointment was added to the calendar!"))
            .then(this.setState({
                title: "", allDay: "", start: "", end: ""
            }))
            .then(res => console.log(res))
            .catch(error => console.log(error));
        window.location.href = "/auth/employees_profile";


    };
    render() {


        return (
            <div className="container">
                <div className="row">
                    <div className="col-md-12" style={{ border: '10px double #9370DB', background: "white", color: 'black', marginBottom: "30px" }}>

                        <form className="white" onSubmit={this.handleSubmit.bind(this)} style={{ marginBottom: "50px" }}>
                            <h2 className="grey-text text-darken-3" style={{ textAlign: "center", marginTop: "15px" }}>Add an Appointment to</h2><br></br>
                            <h2 style={{ textAlign: "center" }}>Dianis' Schedule</h2>
                            <hr style={{ background: "white" }}></hr>
                            <div className="input-field">
                                <label htmlFor="title">* Details (Pet Name, Breed, Service, Phone, etc)</label>
                                <input className="form-control" style={{ float: "right" }} type="text" id='title' value={this.state.title} onChange={this.handleChange} />
                            </div>
                            <div className="input-field">
                                <label htmlFor="allDay">* All Day? (False default)</label>
                                <input className="form-control" style={{ float: "right", marginBottom: "15px" }} type="text" id='allDay' value={this.state.allDay} onChange={this.handleChange} />
                            </div>
                            <div className="input-field" style={{ marginTop: "25px" }}>
                                <h6>Start Date and Time</h6>
                                <DateTimePicker
                                    style={{ color: "black" }}
                                    onChange={this.onChangeDatePicker}
                                    value={this.state.start}
                                />
                                {/* <label htmlFor="start">* Start Time (YYYY-MM-DD HH:MM:SS)</label>
                                <input className="form-control" style={{ float: "right" }} type="text" id='start' value={this.state.start} onChange={this.handleChange} /> */}
                            </div>
                            <div className="input-field" style={{ marginTop: "25px" }}>
                                <h6>End Date and Time</h6>
                                <DateTimePicker
                                    style={{ color: "black" }}
                                    onChange={this.onChangeDatePickerEnd}
                                    value={this.state.end}
                                />
                                {/* <label htmlFor="end">* End Time (YYYY-MM-DD HH:MM:SS)</label>
                                <input className="form-control" style={{ float: "right", marginBottom: "15px" }} type="text" id='end' value={this.state.end} onChange={this.handleChange} /> */}
                            </div>
                            <div className="" >
                                <button style={{ marginTop: "15px" }} className="btn-primary btn-block"
                                >Submit</button>
                            </div>
                        </form>
                    </div>
                </div>
            </div>
        )
    }
}

export default AddAppointmentFormEmp2;