import axios from 'axios';
import { Button } from 'reactstrap';
import React, { Component } from 'react';
import MainCalendar from '../../components/Calendar';
import AddAppointmentFormAdmin from '../../components/AddAppointmentForm';
import AddAppointmentFormEmp1 from '../../components/AddAppointmentFormEmp1';
import AddAppointmentFormEmp2 from '../../components/AddAppointmentFormEmp2';

class Profile extends Component {

    state = {
        employee: '',
        isLoading: true,
        error: false,
        toggleAddAppointmentForm: false
    }

    toggleAddAppointmentFunction = () => {
        this.setState({
            toggleAddAppointmentForm: !this.state.toggleAddAppointmentForm
        })
    }

    async componentDidMount() {
        
        const accessString = localStorage.getItem('JWT');
        if (accessString == null) {
            this.setState({
                isLoading: false,
                error: true,
            });
        } else {
            try {
                const response = await axios.get('/auth/employees_profile', {
                    headers: { Authorization: `JWT ${accessString}` }
                });
                this.setState({
                    employee: response.data,
                    isLoading: false,
                    error: false,
                });
                console.log(response)
            } catch (error) {
                console.error(error.response);
                this.setState({
                    error: true,
                });
            }

        }
    }

    //Logout User
    handleLogOut(e) {
        e.preventDefault();
        localStorage.removeItem('JWT');
        window.location.href = "/auth/login"
    }

    render() {
        // const cal_events = [];
        const {
            isLoading,
            error
        } = this.state
        if (error) {
            return (
                <div>
                    Problem fetching user data. Please login again.
                </div>
            )
        }
        if (isLoading) {
            return (
                <div>Loading User Data...</div>
            )
        }

        return (
            <div className="container">
                <div className="row">
                    <div className="col-md-12">
                        <hr style={{ borderTop: "1px solid black", height: "0" }}></hr>
                        <h1 style={{ textAlign: "center", textShadow: "-1px 0 white, 0 1px white, 1px 0 white, 0 -1px white", marginBottom: "20px", fontSize: "50px" }}>Welcome to Amazing Pet Grooming profile</h1>
                        <hr style={{ borderTop: "1px solid black", height: "0" }}></hr>
                        <Button color="warning" style={{ fontSize: "20px", color: "navy", border: "solid 1px navy", marginBottom: "15px" }} onClick={this.handleLogOut}>Logout</Button>
                        <Button style={{ fontSize: "20px", background: "rgba(3, 26, 77, 0.952)", marginLeft: "15px", marginBottom: "15px" }} onClick={this.toggleAddAppointmentFunction}>Click to add Appointments <i className="fas fa-chevron-circle-down"></i></Button>
                        {
                            this.state.toggleAddAppointmentForm ? (
                                <div className="container" style={{ background: "white", paddingTop: "20px" }}>
                                    <div className="row">
                                        <div className="col-md-4">
                                            <AddAppointmentFormAdmin />
                                        </div>
                                        <div className="col-md-4">
                                            <AddAppointmentFormEmp1 />
                                        </div>
                                        <div className="col-md-4">
                                            <AddAppointmentFormEmp2 />
                                        </div>
                                    </div>
                                </div>
                            ) : (
                                    null
                                )
                        }
                    </div>
                    <MainCalendar />
                </div>
            </div>

        )
    }
}

export default Profile;
